<template>
<div>
  <base-header class="pb-6 pb-8 pt-5 pt-md-8 bg-gradient-success">
    <b-row class="justify-content-center">
      <b-col xl="6" md="12">
        <h1 class="display-2 text-white">{{$t('lang.createTokenSloganTitle')}}</h1>
        <p class="text-white mt-0 mb-5">{{$t('lang.createTokenSloganMore')}}</p>
      </b-col>
    </b-row>
  </base-header>
  <b-container fluid class="mt--7">
    <b-row class="justify-content-center">
      <b-col xl="6" md="12">
        <card>
          <b-row align-v="center" slot="header" >
            <b-col cols="8">
              <h3 class="mb-0">{{$t('lang.createTokenTxt1')}} </h3>
            </b-col>
            <!-- <b-col cols="4" class="text-right">
              <a href="#" class="h4 mb-0 d-none d-lg-inline-block active router-link-active" target="balk" style="text-decoration: underline;"> {{$t('lang.createTokenTxt2')}} </a>
            </b-col> -->
          </b-row>

          <b-form @submit.prevent="updateProfile">
            <h6 class="heading-small text-muted mb-4">{{$t('lang.createTokenTxt3')}}</h6>

            <div class="pl-lg-4">
              <b-row>
                <b-col lg="6">
                  <base-input
                    type="text"
                    :label="$t('lang.createTokenTxt4')"
                    :placeholder="$t('lang.createTokenTxt5')"
                    v-model="token.symbol"
                  >
                  </base-input>
                </b-col>
                <b-col lg="6">
                  <base-input
                    type="text"
                    :label="$t('lang.createTokenTxt6')"
                    placeholder="0-99,999,999,999,999,999"
                    v-model="token.supply"
                  >
                  </base-input>
                </b-col>
              </b-row>
              <b-row >
                <b-col lg="6">
                  <base-input
                    type="text"
                    :label="$t('lang.createTokenTxt7')"
                    :placeholder="$t('lang.createTokenTxt8')"
                    v-model="token.name"
                  >
                  </base-input>
                </b-col>
                <b-col lg="6">
                  <base-input
                    type="text"
                    :label="$t('lang.createTokenTxt9')"
                    placeholder="18"
                    v-model="token.decimals"
                  >
                  </base-input>
                </b-col>
              </b-row>
            </div>
            <hr class="my-4">

            <!-- Address -->
            <h6 class="heading-small text-muted mb-4">{{$t('lang.createTokenTxt10')}}</h6>

            <div class="pl-lg-4">
              <b-row>
                <b-col md="12">
                  <base-input
                    type="text"
                    :label="$t('lang.createTokenTxt11')"
                    placeholder="0"
                    v-model="token.burn"
                  >
                  </base-input>
                </b-col>
              </b-row>
              <b-row>
                <b-col md="12">
                  <base-input
                    type="text"
                    :label="$t('lang.createTokenTxt12')"
                    placeholder="0"
                    v-model="token.fees"
                  >
                  </base-input>
                </b-col>
              </b-row>
              <b-row>
                <b-col md="12">
                  <b-row>
                    <b-col md="10">
                      <p style="color: #525f7f; font-size: 0.875rem; font-weight: 600;">
                        {{$t('lang.createTokenTxt13')}}
                      </p>
                    </b-col>
                    <b-col md="2">
                      <base-checkbox v-model="token.increase"></base-checkbox>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
            </div>

            <hr class="my-4">
            <!-- Description -->
            <h6 class="heading-small text-muted mb-4">{{$t('lang.createTokenTxt14')}}</h6>
            <div class="pl-lg-4">
              <a href="javascript:void(0)" class="btn btn-info" style="width: 100%" v-if="!account" @click="handleLogin">{{$t('lang.connectWallet')}}</a>
              <a href="javascript:void(0)" class="btn btn-info" style="width: 100%" v-else @click="createToken">{{$t('lang.createToken')}}</a>
            </div>

          </b-form>
        </card>
      </b-col>
    </b-row>

    <div class="mt-5"></div>

    <b-row>
      <b-col>
        <b-card no-body>
            <b-card-header class="border-0">
                <h3 class="mb-0">{{$t('lang.createTokenTxt15')}} 
                  <a href="javascript:void(0)" class="btn btn-info" v-if="!account" @click="handleLogin">{{$t('lang.connectWallet')}}</a>
                  <a href="javascript:void(0)" class="btn btn-info" v-else @click="refreshMyTokenList">{{$t('lang.refresh')}}</a>
                </h3>
            </b-card-header>

            <el-table class="table-responsive table"
                      header-row-class-name="thead-light"
                      :data="projects">
                <!-- <el-table-column label="Token Symbol"
                                min-width="200px"
                                prop="symbol">
                    <template v-slot="{row}">
                        <b-media no-body class="align-items-center">
                            <a href="#" class="avatar rounded-circle mr-3">
                                <img alt="Image placeholder" :src="row.img">
                            </a>
                            <b-media-body>
                                <span class="font-weight-600 name mb-0 text-sm">{{row.title}}</span>
                            </b-media-body>
                        </b-media>
                    </template>
                </el-table-column> -->

                <el-table-column :label="$t('lang.createTokenTxt4')"
                                prop="symbol"
                                min-width="80px">
                </el-table-column>

                <el-table-column :label="$t('lang.createTokenTxt7')"
                                prop="name"
                                min-width="80px">
                </el-table-column>

                <el-table-column :label="$t('lang.createTokenTxt6')"
                                prop="totalSupply"
                                min-width="100px">
                </el-table-column>
                
                <el-table-column :label="$t('lang.createTokenTxt9')"
                                prop="decimals"
                                min-width="80px">
                </el-table-column>
                
                <el-table-column :label="$t('lang.address')"
                                prop="address"
                                min-width="150px">
                </el-table-column>
                
                <el-table-column :label="$t('lang.createTokenTxt16')"
                                prop="increase"
                                min-width="140px">
                    <template v-slot="{row}">
                        <b-row v-if="row.increase == 'Y'">
                          <b-col md="4">
                            <base-input
                              type="text"
                              :label="$t('lang.address')"
                              placeholder="0x..."
                              v-model="mintAddress"
                            >
                            </base-input>
                          </b-col>
                          <b-col md="4">
                            <base-input
                              type="text"
                              :label="$t('lang.amount')"
                              placeholder="0"
                              v-model="mintAmount"
                            >
                            </base-input>
                          </b-col>
                          <b-col md="4">
                            <a href="javascript:void(0)" class="btn btn-info" @click="mint(row.address, row.decimals)"> {{$t('lang.mint')}} </a>
                          </b-col>
                        </b-row>
                        <span v-else>{{row.increase}}</span>
                    </template>
                </el-table-column>
            </el-table>

            <b-card-footer class="py-4 d-flex justify-content-end">
                <base-pagination v-model="currentPage" :per-page="1000" :total="pageTotal"></base-pagination>
            </b-card-footer>
        </b-card>
      </b-col>
    </b-row>
  </b-container>
</div>
</template>
<script>
// import projects from './projects'
import { Table, TableColumn} from 'element-ui'
import { useERC20V3FactoryContract, useCustomErc20Contract } from '@/utils/useContract';
import { mapState } from 'vuex';
import { formatAmount, parseAmount } from '@/utils/format';
export default {
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      token: {
        symbol: '',
        supply: '',
        name: '',
        decimals: '',
        burn: '',
        fees: '',
        increase: false
      },
      projects: [],
      currentPage: 1,
      pageTotal: 0,
      mintAmount: 0,
      mintAddress: ''
    };
  },
  computed: {
    ...mapState(['account', 'provider', 'gasPrice', 'slippage', 'deadline', 'multipath']),
  },
  async created() {
    this.refreshMyTokenList();
  },
  methods: {
    updateProfile() {
      alert('Your data: ' + JSON.stringify(this.user));
    },
    handleLogin() {
      this.$store.dispatch('setWebProvider');
      this.refreshMyTokenList();
    },
    async createToken() {
      if(!this.account){
        throw 'No connect to a wallet!';
      }
      let isBurn = false;
      let isFees = false;
      if(this.token.burn != '' && this.token.burn != 0){
        isBurn = true;
      }
      if(this.token.fees != '' && this.token.fees != 0){
        isFees = true;
      }

      const erc20V3FactoryContract = useERC20V3FactoryContract();
      
      if(!isBurn && !isFees && !this.token.increase){
        erc20V3FactoryContract.methods
        .createStdERC20(parseAmount(this.token.supply, this.token.decimals), 
          this.token.name, 
          this.token.symbol, 
          this.token.decimals)
        .send({ from: this.account, value: 80000000000000000 })
        .on('transactionHash', hash => {
          this.$message({ type: 'success', title: 'Create token', message: 'Create token success!', hash: hash });
        })
        .on('receipt', receipt => {})
        .on('error', function (error, receipt) {
          throw error;
        });
      }
      
      if((isBurn || isFees) && !this.token.increase){
        erc20V3FactoryContract.methods
        .createCustomERC20(parseAmount(this.token.supply, this.token.decimals), 
          this.token.name, 
          this.token.symbol, 
          this.token.decimals, 
          isBurn?this.token.burn * 100:0,
          isFees?this.token.fees * 100:0,
          this.account)
        .send({ from: this.account, value: 80000000000000000 })
        .on('transactionHash', hash => {
          this.$message({ type: 'success', title: 'Create token', message: 'Create token success!', hash: hash });
        })
        .on('receipt', receipt => {})
        .on('error', function (error, receipt) {
          throw error;
        });
      }
      
      if(this.token.increase){
        erc20V3FactoryContract.methods
        .createCustomMintableERC20(parseAmount(this.token.supply, this.token.decimals), 
          this.token.name, 
          this.token.symbol, 
          this.token.decimals, 
          isBurn?this.token.burn * 100:0,
          isFees?this.token.fees * 100:0,
          this.account)
        .send({ from: this.account, value: 80000000000000000 })
        .on('transactionHash', hash => {
          this.$message({ type: 'success', title: 'Create token', message: 'Create token success!', hash: hash });
        })
        .on('receipt', receipt => {})
        .on('error', function (error, receipt) {
          throw error;
        });
      }
    },
    async refreshMyTokenList() {
      this.projects = [];
      if(this.account){
        const erc20V3FactoryContract = useERC20V3FactoryContract();
        const myTokenList = await erc20V3FactoryContract.methods.getTokenByUser(this.account).call();
        this.pageTotal = myTokenList[0].length + myTokenList[1].length + myTokenList[2].length;
        for(let i=0; i< myTokenList[0].length; i++){
          const erc20Contract = useCustomErc20Contract(myTokenList[0][i]);
          const symbol = await erc20Contract.methods.symbol().call();
          const name = await erc20Contract.methods.name().call();
          const totalSupply = await erc20Contract.methods.totalSupply().call();
          const decimals = await erc20Contract.methods.decimals().call();
          // const tradeBurnRatio = await erc20Contract.methods.tradeBurnRatio().call();
          // const tradeFeeRatio = await erc20Contract.methods.tradeFeeRatio().call();
          console.info(erc20Contract)
          let project = {
            symbol: symbol,
            name: name,
            totalSupply: formatAmount(totalSupply, decimals),
            decimals: decimals,
            address: myTokenList[0][i],
            increase: 'No'
          }
          this.projects.push(project)
        }
        for(let i=0; i< myTokenList[1].length; i++){
          const erc20Contract = useCustomErc20Contract(myTokenList[1][i]);
          const symbol = await erc20Contract.methods.symbol().call();
          const name = await erc20Contract.methods.name().call();
          const totalSupply = await erc20Contract.methods.totalSupply().call();
          const decimals = await erc20Contract.methods.decimals().call();
          // const tradeBurnRatio = await erc20Contract.methods.tradeBurnRatio().call();
          // const tradeFeeRatio = await erc20Contract.methods.tradeFeeRatio().call();
          console.info(erc20Contract)
          let project = {
            symbol: symbol,
            name: name,
            totalSupply: formatAmount(totalSupply, decimals),
            decimals: decimals,
            address: myTokenList[1][i],
            increase: 'No'
          }
          this.projects.push(project)
        }
        for(let i=0; i< myTokenList[2].length; i++){
          const erc20Contract = useCustomErc20Contract(myTokenList[2][i]);
          const symbol = await erc20Contract.methods.symbol().call();
          const name = await erc20Contract.methods.name().call();
          const totalSupply = await erc20Contract.methods.totalSupply().call();
          const decimals = await erc20Contract.methods.decimals().call();
          // const tradeBurnRatio = await erc20Contract.methods.tradeBurnRatio().call();
          // const tradeFeeRatio = await erc20Contract.methods.tradeFeeRatio().call();
          console.info(erc20Contract)
          let project = {
            symbol: symbol,
            name: name,
            totalSupply: formatAmount(totalSupply, decimals),
            decimals: decimals,
            address: myTokenList[2][i],
            increase: 'Y'
          }
          this.projects.push(project)
        }
      }
    },
    mint(contractAddress, decimals) {
      if(this.mintAddress == '' && this.mintAmount <= 0){
        // this.$message({ type: 'error', title: 'Mint', message: 'Address and Amount can not be null!', hash: '' });
        return;
      }
      const erc20Contract = useCustomErc20Contract(contractAddress);
      erc20Contract.methods
      .mint(this.mintAddress, parseAmount(this.mintAmount, decimals))
      .send({ from: this.account, gas: 210000, gasPrice: 5000000000 })
      .on('transactionHash', hash => {
        this.$message({ type: 'success', title: 'Mint', message: 'Mint success!', hash: hash });
      })
      .on('receipt', receipt => {})
      .on('error', function (error, receipt) {
        throw error;
      });
    }
  }
};
</script>
<style></style>
